import mock from "@/data/mock_cases.json";

type RiskLabel = "ROUTINE" | "ESCALATE" | "BLOCK";
type CaseStatus = "PENDING_REVIEW" | "APPROVED" | "REJECTED" | "NEEDS_INFO" | "CLOSED";

export type CaseRecord = {
  id: string;
  user_name: string;
  user_message: string;
  tool_name: string;
  tool_args_redacted: Record<string, unknown>;
  risk_label: RiskLabel;
  risk_score: number;
  risk_rationale: string;
  policy_refs: string[];
  status: CaseStatus;
  created_at: string;
  audit_trail: Array<{
    ts: string;
    actor: string;
    action: string;
    detail?: string;
  }>;
};

export type Decision = "APPROVE" | "REJECT" | "REQUEST_INFO";

const store = new Map<string, CaseRecord>();

function initOnce() {
  if (store.size > 0) return;

  const seed = mock as any as CaseRecord[];
  for (const c of seed) store.set(c.id, c);
}

function nowIso() {
  return new Date().toISOString();
}

export function listCases(): CaseRecord[] {
  initOnce();
  return Array.from(store.values());
}

export function getCaseById(id: string): CaseRecord | null {
  initOnce();
  return store.get(id) ?? null;
}

export function decideCase(id: string, decision: Decision, note?: string): CaseRecord | null {
  initOnce();
  const c = store.get(id);
  if (!c) return null;

  let nextStatus: CaseRecord["status"] = c.status;
  if (decision === "APPROVE") nextStatus = "APPROVED";
  if (decision === "REJECT") nextStatus = "REJECTED";
  if (decision === "REQUEST_INFO") nextStatus = "NEEDS_INFO";

  const audit = {
    ts: nowIso(),
    actor: "hitl_ui",
    action: decision.toLowerCase(),
    detail: note?.trim() ? note.trim() : undefined,
  };

  const updated: CaseRecord = {
    ...c,
    status: nextStatus,
    audit_trail: [...(c.audit_trail ?? []), audit],
  };

  store.set(id, updated);
  return updated;
}
